			"regras": "Queda do PIB  momentnea e Brasil tem condies para retomada, diz Dilma\n\nA presidente Dilma Rousseff (PT) disse, nesta sexta-feira (29), que o resultado ruim do PIB (Produto Interno Bruto), apresentado nos dois ltimos trimestres,  momentneo. Ela lembrou dos feriados durante a Copa do Mundo e comparou o desempenho da economia brasileira com a de outros pases.\n  Eu acredito que esse resultado do PIB  momentneo.  deste trimestre. Um dos motivos que explica esse resultado  o nmero de feriados que ns tivemos por conta da Copa do Mundo.\nEm discurso realizado em Salvador (BA), Dilma tambm afirmou que acredita em uma recuperao da economia j no prximo semestre.\n Acredito que no segundo semestre ns teremos uma grande recuperao. O Brasil tem hoje todas as condies para ter hoje uma grande retomada. Ns estamos criando essas condies.\nA presidente, que  candidata  reeleio, lembrou que outros pases tambm enfrentam problemas em suas economias.\n Tm trs pases que se saram bem do segundo trimestre no mundo. Um  a China, outro  os Estados Unidos e o outro  o Reino Unido. Nos demais pases voc tem uma reduo drstica do crescimento, inclusive aqui na Amrica Latina se verifica isso. Uma das explicaes foi a queda no preo das commodities\nO Produto Interno Bruto do Brasil caiu 0,6% no segundo trimestre sobre os primeiros trs meses do ano, segundo dados divulgados pelo IBGE nesta sexta-feira (29). Como o instituto revisou para baixo o resultado do primeiro trimestre, o Pas entrou em recesso tcnica. \nPIB brasileiro cai 0,6% no 2 trimestre, diz IBGE\nO PIB (Produto Interno Bruto) nacional encolheu 0,6% ao longo do segundo trimestre de 2014, na comparao com o primeiro do ano. O resultado, divulgado nesta sexta-feira (29) pelo IBGE (Instituto Brasileiro de Geografia e Estatstica), leva em conta todas as riquezas produzidas pelo Pas.O instituto tambm aproveitou para revisar a divulgao do resultado referente ao primeiro trimestre do ano, que passou de 0,2% para -0,2%, o que caracteriza que o Brasil agora se encontra em uma recesso tcnica, situao na qual a economia encolhe por dois trimestres seguidos.\nApesar das duas quedas seguidas, no acumulado dos quatro ltimos trimestres, houve crescimento de 1,4% em relao aos quatro trimestres anteriores.\nEm valores correntes, a produo das riquezas do Pas alcanou R 1,27 trilho no segundo trimestre, sendo R 1,1 trilho referentes ao valor adicionado a preos bsicos e R 183,7 bilhes aos Impostos sobre Produtos lquidos de Subsdios.\n\nveja estes link tbm\nhttp://dikadeartigos.blogspot.com.br/\nhttp://dikadeartigos.blogspot.com.br/\nhttp://dikadeartigos.blogspot.com.br/",
